import { useEffect, useState } from "react";
import "./chat.css";
import EmojiPicker from "emoji-picker-react";
import { useRef } from "react";
import {arrayUnion, doc,getDoc,onSnapshot, updateDoc} from "firebase/firestore";
import {db} from "../../lib/firebase";
import { useChatStore } from "../../lib/chatStore";
import { toast } from "react-toastify";
import { useUserStore } from "../../lib/userStore";
import { upload } from "@testing-library/user-event/dist/upload";


const Chat = () => {
  const [open,setOpen] = useState(false);
  const [chat,setChat] = useState();
  const [img,setImg] = useState({
    file:null,
    url:"",
  });
  const [text,setText] = useState("");
  const { chatId,user,iscurrentUserBlocked,isRecieverBlocked }=useChatStore();
  const { currentUser }=useUserStore();

  const endRef = useRef(null)

  useEffect(()=>{
    endRef.current.scrollIntoView({behavior:"smooth"});
  },[])

  

  useEffect(()=>{
    const unSub=onSnapshot(doc(db,"chats",chatId),(res)=>{
      setChat(res.data())
    })

    return ()=>{
      unSub();
    };
  },[chatId]);

  const handleEmoji=e=>{
    setText(prev=>prev+e.emoji) 
      setOpen(false)
  };

  const handleImg = e =>{
    if (e.target.files[0]) {
        setImg({
            file:e.target.files[0],
            url: URL.createObjectURL(e.target.files[0])
        });
    }
  };

  const handleSend=async()=>{
    if(text === "") return;

    let imgURL=null;
    try{

      if(img.file){
        imgURL=await upload(img.file);
      }

      await updateDoc(doc(db,"chats",chatId),{
        messages:arrayUnion({
          senderId: currentUser.id,
          text,
          createdAt: new Date(),
          ...(imgURL && {img:imgURL}),
        }),
      });

      const userIDs=[currentUser.id,user.id]

      userIDs.forEach(async(id)=>{
        
        const userChatRef=doc(db,"userchats",id); 
        const userChatsSnapshot= await getDoc(userChatRef);
        
        if(userChatsSnapshot.exists()){
          const userChatsData=userChatsSnapshot.data(); 
          
          const chatIndex=userChatsData.chats.findIndex((c)=>c.chatId===chatId);
          
          userChatsData.chats[chatIndex].lastMessage=text;
          userChatsData.chats[chatIndex].isSeen= id === currentUser.id ? true : false;
          userChatsData.chats[chatIndex].updatedAt=Date.now();
          
          await updateDoc(userChatRef,{
            chats:userChatsData.chats,
          });
        }
      });
    }catch(err){
      console.log(err);
      toast.error(err.message);
    }

    setImg({
      file:null,
      url:"",
    });

    setText("");
  };
  
  return (
    <div className='chat'>
      <div className="top">
        <div className="user">
          <img src={user?.avatar || "./avatar.png"} alt="" />
          <div className="texts">
            <span>{user?.username}</span>
            <p>Lorem ipsum, dolor sit amet.</p>
          </div>
        </div>
        <div className="icons">
          <img src="./phone.png" alt="" />
          <img src="./video.png" alt="" />
          <img src="./info.png" alt="" />
        </div>
      </div>
      <div className="center">
        {chat?.messages?.map((message)=>(
          <div className={message.senderId===currentUser?.id ? "message own" : "message"} key={message?.createAt}>
          <div className="texts">
            {message.img && <img src={message.img} alt="" />}
            <p>{message.text}</p>
            {/* <span>{message.}</span> */}
          </div>
          </div>
        ))}

       {img.url && <div className="message own">
          <div className="texts">
            <img src={img.url} alt="" />
          </div>
        </div>}
        <div ref={endRef}></div>
      </div>
      <div className="bottom">
        <div className="icons">
          <label htmlFor="file">  
            <img src="./img.png" alt="" />
          </label>
          <input type="file" id="file" style={{display:"none"}} onChange={handleImg}/>
          <img src="./camera.png" alt="" />
          <img src="./mic.png" alt="" />
        </div>
        <input type="text" placeholder={(iscurrentUserBlocked || isRecieverBlocked) ? "You cannot send a message" : "Type a message..."} value={text} onChange={(e)=>setText(e.target.value)}
        disabled={iscurrentUserBlocked || isRecieverBlocked}/>
        <div className="emoji">
          <img src="./emoji.png" alt="" onClick={()=>setOpen((prev)=>!prev)}/>
          <div className="picker">
            <EmojiPicker open={open} onEmojiClick={handleEmoji}/>
          </div>
        </div>
        <button className="sendButton" onClick={handleSend} disabled={iscurrentUserBlocked || isRecieverBlocked}>Send</button>
      </div>
    </div>
  )
}

export default Chat